const prefixer = code => '____sharedrop__' + code;
function setData(code, data) {
    let allData = localStorage.getItem(prefixer(''));
    if(allData) {
        allData = JSON.parse(allData);
        allData.push({code, response: data})
    } else {
        allData = [{ code, response: data }]

    }

    localStorage.setItem(prefixer(''), JSON.stringify(allData));


}

function getData() {
    let allData = localStorage.getItem(prefixer(''));

    if (allData) { 
        return JSON.parse(allData).reverse();
    }

}
function clear() {
    localStorage.setItem(prefixer(''), '[]');
}



    var textArea,
        copy;

    function isOS() {
        return navigator.userAgent.match(/ipad|iphone/i);
    }

    function createTextArea(text) {
      
        textArea = document.createElement('textArea');
        textArea.value = text;
        document.body.appendChild(textArea);
    }

    function selectText() {
        var range,
            selection;

        if (isOS()) {
            console.log(textArea.value)
            range = document.createRange();
            range.selectNodeContents(textArea);
            selection = window.getSelection();
            selection.removeAllRanges();
            selection.addRange(range);
            textArea.setSelectionRange(0, 999999);
        } else {
            textArea.select();
        }
    }

    function copyToClipboard() {
        document.execCommand('copy');
        document.body.removeChild(textArea);
    }

    copy = function (text) {
        createTextArea(text);
        selectText();
        copyToClipboard();
    };
    //       navigator.clipboard.writeText(text);



module.exports.util = { setData, getData, clear, copy}